<template>
  <div class="add-topic">
    <el-form
      ref="ruleForm"
      :model="ruleForm"
      :rules="rules"
      label-width="110px"
      class="demo-ruleForm"
    >
      <el-form-item label="题型" prop="type">
        <el-select
          v-model="ruleForm.type"
          placeholder="请选择"
          class="select-input"
          :disabled="$route.name === 'seeTopic'"
          @change="setStructure()"
        >
          <el-option label="单选题" value="radio" />
          <el-option label="多选题" value="checkbox" />
          <el-option label="简答题" value="short_answer" />
        </el-select>
      </el-form-item>
      <el-form-item label="试题难度" prop="difficulty">
        <el-select
          v-model="ruleForm.difficulty"
          placeholder="请选择"
          class="select-input"
          :disabled="$route.name === 'seeTopic'"
        >
          <el-option label="简单" value="1" />
          <el-option label="正常" value="2" />
          <el-option label="难" value="3" />
        </el-select>
      </el-form-item>
      <el-form-item label="单节课名称" prop="single">
        <el-select
          v-model="ruleForm.single"
          placeholder="请选择"
          class="select-input"
          :disabled="$route.name === 'seeTopic'"
        >
          <el-option
            v-for="(item, index) of singleList"
            :key="index"
            :label="item.title"
            :value="item._id"
          />
        </el-select>
      </el-form-item>
      <el-form-item label="创建人" prop="name" class="select-input">
        <el-select
          v-model="ruleForm.name"
          placeholder="请选择"
          class="select-input"
          :disabled="true"
        >
          <el-option
            v-for="(item, index) of createNameList"
            :key="index"
            :label="item.name"
            :value="item._id"
          />
        </el-select>
      </el-form-item>
      <el-form-item label="题干" prop="stem">
        <div
          v-show="!stemShow"
          :disabled="$route.name === 'seeTopic'"
          :class="$route.name === 'seeTopic' ? 'disabled' : ''"
          class="div-textarea"
          :style="{ color: ruleForm.stem ? '#515a6e' : '' }"
          @click="stemClick"
          v-html="ruleForm.stem || '请输入题干内容'"
        />
        <vue-editor
          v-if="stemShow"
          id="editor"
          ref="editor"
          v-model="ruleForm.stem"
          :use-custom-image-handler="true"
          @image-added="handleImageAdded"
        />
      </el-form-item>
      <el-form-item
        v-if="ruleForm.type !== 'short_answer'"
        label="选项设置"
        prop="option"
      >
        <el-checkbox-group
          v-if="ruleForm.type === 'checkbox'"
          v-model="answers"
          :disabled="$route.name === 'seeTopic'"
        >
          <template v-for="(item, index) of ruleForm.option">
            <el-checkbox
              :key="index"
              :label="index"
              name="option"
              @click.native="getFocus()"
            >
              <div
                v-show="optionIndex !== index"
                :disabled="$route.name === 'seeTopic'"
                :class="$route.name === 'seeTopic' ? 'disabled' : ''"
                class="div-textarea"
                style="height: 40px;"
                :style="{ color: item.item ? '#515a6e' : '' }"
                @click="optionClick(index)"
                v-html="item.item || '请输入选项内容'"
              />
              <vue-editor
                v-if="optionIndex === index"
                ref="editor"
                v-model="item.item"
                placeholder="请输入选项内容"
                :use-custom-image-handler="true"
                @text-change="getOptionContent(item.item, index)"
                @image-added="handleImageAdded"
              />
              <svg
                v-if="
                  index === ruleForm.option.length - 1 &&
                    index > 1 &&
                    $route.name !== 'seeTopic'
                "
                aria-hidden="true"
                class="del-icon"
                @click="delOption(index)"
              >
                <use xlink:href="#icon-btn-pic-closex-copy" />
              </svg>
              <p v-if="!item.item" class="error-tip">{{ errorTip }}</p>
            </el-checkbox>
          </template>
        </el-checkbox-group>
        <el-radio-group
          v-if="ruleForm.type === 'radio'"
          v-model="answers"
          :disabled="$route.name === 'seeTopic'"
        >
          <template v-for="(item, index) of ruleForm.option">
            <el-radio
              :key="index"
              :label="index"
              name="option"
              @click.native="getFocus()"
            >
              <div
                v-show="optionIndex !== index"
                :disabled="$route.name === 'seeTopic'"
                :class="$route.name === 'seeTopic' ? 'disabled' : ''"
                class="div-textarea"
                style="height: 40px;"
                :style="{ color: item.item ? '#515a6e' : '' }"
                @click="optionClick(index)"
                v-html="item.item || '请输入选项内容'"
              />
              <vue-editor
                v-if="optionIndex === index"
                ref="editor"
                v-model="item.item"
                placeholder="请输入选项内容"
                :use-custom-image-handler="true"
                @text-change="getOptionContent(item.item, index)"
                @image-added="handleImageAdded"
              />
              <svg
                v-if="index > 1 && $route.name !== 'seeTopic'"
                aria-hidden="true"
                class="del-icon"
                @click="delOption(index)"
              >
                <use xlink:href="#icon-btn-pic-closex-copy" />
              </svg>
              <p v-if="item.item === ''" class="error-tip">
                {{ errorMessage[index] }}
              </p>
            </el-radio>
          </template>
        </el-radio-group>
        <div class="center">
          <el-button
            type="primary"
            icon="el-icon-plus"
            :disabled="$route.name === 'seeTopic'"
            @click="addOption"
            >添加一个选项</el-button
          >
        </div>
      </el-form-item>
      <el-form-item
        v-if="ruleForm.type === 'short_answer'"
        label="用户问题确诊"
        prop="user_problem"
      >
        <div
          v-show="!userProblemShow"
          :disabled="$route.name === 'seeTopic'"
          :class="$route.name === 'seeTopic' ? 'disabled' : ''"
          class="div-textarea"
          :style="{ color: ruleForm.user_problem ? '#515a6e' : '' }"
          @click="userProblemClick"
          v-html="ruleForm.user_problem || '请输入用户问题确诊答案'"
        />
        <vue-editor
          v-if="userProblemShow"
          ref="editor"
          v-model="ruleForm.user_problem"
          placeholder="请输入用户问题确诊答案"
          :use-custom-image-handler="true"
          @image-added="handleImageAdded"
        />
      </el-form-item>
      <el-form-item
        v-if="ruleForm.type === 'short_answer'"
        label="问诊部分"
        prop="interrogation"
      >
        <div
          v-show="!interrogationShow"
          :disabled="$route.name === 'seeTopic'"
          :class="$route.name === 'seeTopic' ? 'disabled' : ''"
          class="div-textarea"
          :style="{ color: ruleForm.interrogation ? '#515a6e' : '' }"
          @click="interrogationClick"
          v-html="ruleForm.interrogation || '请输入针对用户问题问诊的答案'"
        />
        <vue-editor
          v-if="interrogationShow"
          ref="editor"
          v-model="ruleForm.interrogation"
          placeholder="请输入针对用户问题问诊的答案"
          :use-custom-image-handler="true"
          @image-added="handleImageAdded"
        />
      </el-form-item>
      <el-form-item label="答案解析" prop="answer_analysis">
        <div
          v-show="!answerAnalysisShow"
          :disabled="$route.name === 'seeTopic'"
          :class="$route.name === 'seeTopic' ? 'disabled' : ''"
          class="div-textarea"
          :style="{ color: ruleForm.answer_analysis ? '#515a6e' : '' }"
          @click="answerAnalysisClick"
          v-html="ruleForm.answer_analysis || '请输入答案解析'"
        />
        <vue-editor
          v-if="answerAnalysisShow"
          ref="editor"
          v-model="ruleForm.answer_analysis"
          placeholder="请输入答案解析"
          :use-custom-image-handler="true"
          @image-added="handleImageAdded"
        />
      </el-form-item>
      <el-form-item>
        <el-button
          type="primary"
          :disabled="$route.name === 'seeTopic'"
          @click="submitForm('ruleForm')"
          >确定</el-button
        >
      </el-form-item>
    </el-form>
    <tip
      v-if="answersNum"
      :tip-show.sync="answersNum"
      btn-text="确定"
      class="center"
      :has-footer="true"
      @btnHandle="answersNum = false"
    >
      <p slot="content">请选择正确的答案个数</p>
    </tip>
  </div>
</template>

<script>
import {
  addQuestion,
  getAllSingle,
  userAll,
  getQuestionDetail,
  editQuestion
} from '@/api/learningAdmin'
import { VueEditor } from 'vue2-editor'
import Tip from '@/components/Dialog'
import { upload } from '@/api/upload'
export default {
  components: {
    VueEditor,
    Tip
  },
  data() {
    return {
      ruleForm: {
        type: 'radio',
        name: this.$store.state.user.id || '',
        difficulty: '',
        single: '',
        stem: '',
        option: [
          { item: '', index: 0 },
          { item: '', index: 1 }
        ],
        answer_analysis: '',
        user_problem: '',
        interrogation: ''
      },
      rules: {
        type: [
          { required: true, message: '请进行选择后再操作', trigger: 'change' }
        ],
        difficulty: [
          { required: true, message: '请进行选择后再操作', trigger: 'change' }
        ],
        single: [
          { required: true, message: '请进行选择后再操作', trigger: 'change' }
        ],
        name: [
          { required: true, message: '请进行选择后再操作', trigger: 'change' }
        ],
        stem: [
          {
            required: true,
            message: '您输入的内容有误，请重新输入',
            trigger: 'change'
          }
        ],
        option: [
          {
            type: 'array',
            required: true,
            message: '请至少选择一个答案',
            trigger: 'change'
          }
        ]
      },
      singleList: [],
      createNameList: [],
      answers: [],
      stemShow: false,
      userProblemShow: false,
      interrogationShow: false,
      answerAnalysisShow: false,
      optionIndex: -1,
      questionDetail: {},
      errorTip: '',
      answersNum: false,
      errorMessage: ['', '']
    }
  },
  async created() {
    this.getAllSingle()
    this.getAllUser()
    if (this.$route.params.id) {
      const { code, data } = await getQuestionDetail(this.$route.params.id)
      if (code === 20000 && data.result) {
        this.questionDetail = data.mtsQuestion
        this.ruleForm.type = this.questionDetail.type
        this.ruleForm.name = this.questionDetail.creator
        this.ruleForm.difficulty = this.questionDetail.difficult
        this.ruleForm.single = this.questionDetail.catalog
        this.ruleForm.stem = this.questionDetail.stem
        if (this.ruleForm.type === 'short_answer') {
          this.ruleForm.user_problem = this.questionDetail.options
        } else this.ruleForm.option = this.questionDetail.options
        this.ruleForm.answer_analysis = this.questionDetail.answer_analysis
        const answer = this.questionDetail.answers
        if (answer) {
          if (Array.isArray(answer)) {
            if (answer.length === 1) this.answers = answer[0]
            else this.answers = answer
          } else this.ruleForm.interrogation = answer
        }
      }
    }
  },
  methods: {
    submitForm(formName) {
      this.$refs[formName].validate(async valid => {
        if (valid) {
          if (this.$route.params.id) {
            let answers, options
            if (this.ruleForm.type === 'radio') answers = [this.answers]
            if (this.ruleForm.type === 'checkbox') answers = [...this.answers]
            if (this.ruleForm.type === 'short_answer') {
              answers = this.ruleForm.interrogation
              options = this.ruleForm.user_problem
            } else options = this.ruleForm.option
            if (
              this.ruleForm.type === 'radio' ||
              this.ruleForm.type === 'checkbox'
            ) {
              this.errorMessage = this.ruleForm.option.map(item => {
                if (item.item) return ''
                else return '选项不能为空'
              })
              const isReturn = this.errorMessage.every(item => {
                return item === ''
              })
              if (!isReturn) return
            }
            if (this.ruleForm.type === 'radio') {
              answers = [this.answers]
              if (answers[0] && answers[0].length === 0) {
                this.answersNum = true
                return
              }
            }
            if (this.ruleForm.type === 'checkbox') {
              answers = [...this.answers]
              if (answers.length <= 1) {
                this.answersNum = true
                return
              }
            }
            const info = {
              catalog: this.ruleForm.single,
              creator: this.ruleForm.name,
              difficult: this.ruleForm.difficulty + '',
              type: this.ruleForm.type,
              stem: this.ruleForm.stem,
              answer_analysis: this.ruleForm.answer_analysis
            }
            if (answers) info.answers = answers
            if (options) info.options = options
            const { data, code } = await editQuestion(
              this.$route.params.id,
              info
            )
            if (code === 20000 && data.result) {
              this.$message({ message: '修改成功', type: 'success' })
              this.$router.push('/learning-admin/topic')
            }
          } else this.addQuestion()
        } else {
          return false
        }
      })
    },
    addOption() {
      this.ruleForm.option.push({
        item: '',
        index: this.ruleForm.option.length
      })
      this.errorMessage.push('')
    },
    async addQuestion() {
      let answers
      if (this.ruleForm.type === 'radio' || this.ruleForm.type === 'checkbox') {
        this.errorMessage = this.ruleForm.option.map(item => {
          if (item.item) return ''
          else return '选项不能为空'
        })
        const isReturn = this.errorMessage.every(item => {
          return item === ''
        })
        if (!isReturn) return
      }
      if (this.ruleForm.type === 'radio') {
        answers = [this.answers]
        if (answers[0] && answers[0].length === 0) {
          this.answersNum = true
          return
        }
      }
      if (this.ruleForm.type === 'checkbox') {
        answers = [...this.answers]
        if (answers.length <= 1) {
          this.answersNum = true
          return
        }
      }
      if (this.ruleForm.type === 'radio' || this.ruleForm.type === 'checkbox') {
        const { code, data } = await addQuestion({
          catalog: this.ruleForm.single,
          creator: this.ruleForm.name,
          difficult: this.ruleForm.difficulty + '',
          type: this.ruleForm.type,
          stem: this.ruleForm.stem,
          options: this.ruleForm.option,
          answers: answers,
          answer_analysis: this.ruleForm.answer_analysis
        })
        if (code === 20000 && data.result) {
          this.$message({ message: '添加成功', type: 'success' })
          this.$router.push('/learning-admin/topic')
        }
      } else {
        const info = {
          catalog: this.ruleForm.single,
          creator: this.ruleForm.name,
          difficult: this.ruleForm.difficulty + '',
          type: this.ruleForm.type,
          stem: this.ruleForm.stem,
          answer_analysis: this.ruleForm.answer_analysis
        }
        if (this.ruleForm.user_problem) {
          info.options = this.ruleForm.user_problem
        }
        if (this.ruleForm.interrogation) {
          info.answers = this.ruleForm.interrogation
        }
        const { code, data } = await addQuestion(info)
        if (code === 20000 && data.result) {
          this.$message({ message: '添加成功', type: 'success' })
          this.$router.push('/learning-admin/topic')
        }
      }
    },
    getFocus() {
      if (!this.$refs.editor) return
      if (!this.$refs.editor[0]) return
      this.$refs.editor[0].quill.enable(true)
      this.$refs.editor[0].quill.focus()
    },
    async getAllSingle() {
      const { code, data } = await getAllSingle()
      if (code === 20000) {
        this.singleList = data.list
      }
    },
    async getAllUser() {
      const { code, data } = await userAll()
      if (code === 20000) this.createNameList = data
    },
    delOption(index) {
      this.ruleForm.option.splice(index, 1)
      this.answers = []
      this.errorMessage.splice(index, 1)
    },
    getOptionContent(content, index) {
      if (!content) this.errorMessage[index] = '选项不能为空'
      else this.errorMessage[index] = ''
    },
    editorFocus() {
      const self = this
      setTimeout(function() {
        if (self.$refs.editor) {
          self.$refs.editor.quill.enable(true)
          self.$refs.editor.quill.focus()
        }
      }, 200)
    },
    answerAnalysisClick() {
      if (this.$route.name === 'seeTopic') return
      this.optionIndex = -1
      this.answerAnalysisShow = true
      this.stemShow = false
      this.userProblemShow = false
      this.interrogationShow = false
      this.editorFocus()
    },
    interrogationClick() {
      if (this.$route.name === 'seeTopic') return
      this.interrogationShow = true
      this.stemShow = false
      this.userProblemShow = false
      this.answerAnalysisShow = false
      this.editorFocus()
    },
    userProblemClick() {
      if (this.$route.name === 'seeTopic') return
      this.userProblemShow = true
      this.interrogationShow = false
      this.stemShow = false
      this.answerAnalysisShow = false
      this.editorFocus()
    },
    stemClick() {
      if (this.$route.name === 'seeTopic') return
      this.stemShow = true
      this.optionIndex = -1
      this.userProblemShow = false
      this.interrogationShow = false
      this.answerAnalysisShow = false
      this.editorFocus()
    },
    optionClick(index) {
      if (this.$route.name === 'seeTopic') return
      this.optionIndex = index
      this.stemShow = false
      this.userProblemShow = false
      this.interrogationShow = false
      this.answerAnalysisShow = false
    },
    setStructure() {
      if (this.ruleForm.type === 'checkbox') this.answers = []
      if (this.ruleForm.type === 'radio') this.answers = ''
    },
    async handleImageAdded(file, Editor, cursorLocation, resetUploader) {
      const data = await upload(file)
      const url = data.url // Get url from response
      Editor.insertEmbed(cursorLocation, 'image', url)
      resetUploader()
    }
  }
}
</script>

<style scoped lang="scss" type="text/scss">
.add-topic {
  .el-checkbox {
    margin-right: 15px;
  }
  .option-setting {
    display: flex;
    .el-input {
      display: inline;
    }
  }
  .select-input {
    /deep/.el-input__inner {
      width: 450px;
    }
  }
  /deep/.el-textarea {
    height: 100px;
    textarea {
      height: 100%;
    }
  }
  /deep/.el-checkbox {
    display: block;
    margin-bottom: 10px;
    .el-checkbox__label {
      width: 100%;
    }
  }
  /deep/.el-radio-group {
    display: block;
  }
  /deep/.el-radio {
    display: block;
    margin-bottom: 10px;
    margin-right: 0;
    .el-radio__label {
      display: inline-block;
      width: 100%;
    }
  }
  /deep/.quillWrapper {
    margin-top: 10px;
    margin-bottom: 10px;
    line-height: normal;
  }
  /deep/.ql-tooltip.ql-editing {
    top: 0 !important;
    right: 137.5px;
    left: auto !important;
  }
  .center {
    text-align: center;
  }
  .del-icon {
    position: absolute;
    top: -2px;
    right: -15px;
    width: 16px;
    height: 16px;
  }
  .error-tip {
    margin-top: 10px;
    color: #f56c6c;
  }
  .div-textarea {
    overflow-y: auto;
    padding: 5px 15px;
    width: 100%;
    height: 100px;
    color: #ccc;
    border: 1px solid #dcdfe6;
    border-radius: 4px;
    box-sizing: border-box;
    cursor: pointer;
    &.disabled {
      color: #c0c4cc;
      background-color: #f5f7fa;
      border-color: #e4e7ed;
      cursor: not-allowed;
    }
  }
  /deep/.el-form-item__content {
    line-height: 20px;
  }
  /deep/.el-radio__input.is-checked + .el-radio__label {
    color: #606266;
  }
}
</style>
